<template>
    <div class="PostList">
        <!-- loading -->
        <div class='loading' v-if='isLoading'>
            <img src="../assets/loading.gif" alt="">
        </div>
        <div class="posts" v-else>
            <ul>
                <li>
                    <div class="toobar">
                    <span class="all">全部</span>
                    <router-link :to="{name:'good'}">
                    <span>精华</span>
                    </router-link>
                    <span>分享</span>
                    <span>问答</span>
                    <span>招聘</span>
                    </div>
                </li>

                <li v-for="post in posts">
                    <!-- 头像 -->
                    <img :src="post.author.avatar_url" alt=" ">

                    <!-- 回复浏览 -->
                    <span class="allcount">
                        <span class="reply_count">{{post.reply_count}}</span>
                        /{{post.visit_count}}
                    </span>

                    <!-- 帖子的分类 -->
                    <span :class="[{put_top:(post.top == true),put_good:(post.good == true),
                    'topiclist-tab':(post.good != true && post.top != true)}]">
                        <span>
                            {{post | tabFormatter}}
                        </span>
                    </span>

                    <!-- 帖子标题    -->
                    <router-link :to="{
                      name:'post_content',
                      params:{
                        id:post.id,
                        name:post.author.loginname
                      }
                    }">
                        <span>{{post.title}}</span>
                    </router-link>

                    <!-- 时间 -->
                    <span class="last_reply">
                         {{post.last_reply_at | formatDate}}
                    </span>
                </li>

                <!-- 页数选择器 -->
                <li>
                  <pagination @handleList="renderList"></pagination>
                 
                </li>
            </ul>
        </div>
    </div>
</template>


<script>
import pagination from './Pagination'
import Vue from 'vue'
import router from '../router'

export default {
    name:'PostList',
    components:{
        pagination
    },
    data(){
        return {
            isLoading:false,
            posts:[],             //代表页面的列表数组
            postpage:1
        }
    },
    methods:{
        getData(){
            this.$http.get('https://cnodejs.org/api/v1/topics',{
              params:{
                page:this.postpage,
                limit:20
              }  
            })
            .then(res=>{
                this.isLoading = false  //加载成功后，将动画去除
                this.posts = res.data.data
            })
            .catch(err=>{
                console.log(err)
            })
        },
        renderList(value){
          this.postpage = value;
          this.getData();
        }
    },
    beforeMount(){
        this.isLoading = true           //加载成功之前显示加载动画
        this.getData()                  //在页面获取之前获取数据
    },
  
    
}
 
</script>


<style scoped>
 .PostList{
    background-color: #e1e1e1;
  }
  .posts {
    margin-top: 10px;
  }

  .PostList img {
    height: 30px;
    width: 30px;
    vertical-align: middle;
  }

  ul {
    list-style: none;
    width: 100%;
    max-width: 1344px;
    margin: 0 auto;
    padding: 0
  }

  ul li:not(:first-child) {
    padding: 9px;
    font-size: 15px;
    font-family: "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma, "Hiragino Sans GB", STHeiti, sans-serif !important;
    font-weight: 400;
    background-color: white;
    color: #333;
    border-top: 1px solid #f0f0f0;
  }

  li:not(:first-child):hover {
    background: #f5f5f5;
  }

  li:last-child:hover {
    background: white;
  }

  li span {
    line-height: 30px;
  }

  .allcount {
    width: 70px;
    display: inline-block;
    text-align: center;
    font-size: 12px;
  }

  .reply_count {
    color: #9e78c0;
    font-size: 14px;
  }

  .put_good, .put_top {
    background: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    color: #fff;
    font-size: 12px;
    margin-right: 10px;
  }
  

  .topiclist-tab {
    background-color: #e5e5e5;
    color: #999;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    font-size: 12px;
    margin-right: 10px;
  }

  .last_reply {
    text-align: right;
    min-width: 50px;
    display: inline-block;
    white-space: nowrap;
    color: #778087;
    font-size: 12px;
    float: right;
  }

  .toobar {
    height: 40px;
    background-color: #f5f5f5;
  }

  .toobar span {
    font-size: 14px;
    color: #80bd01;
    line-height: 40px;
    margin: 0 0 0 30px;
    cursor: pointer;
  }

  .toobar .all{
    background: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    color: #fff;
    font-size: 12px;
    margin-right: 0px;
  }

  .toobar span:hover {
    color: #9e78c0;
  }

  a {
    text-decoration: none;
    color: black;
  }

  a:hover {
    text-decoration: underline;
  }

  .loading {
    text-align: center;
    padding-top: 300px;
  }
</style>



